<template>
<div class="container">
  <button class="btn mr-auto filter-btn" @click="filter = !filter">
      סינון <i class="fas fa-filter"></i>
  </button>
  <form id="filterform" v-if="filter">
    <label for="textfil">הכנס טקסט:</label>
    <input type="text" id="textfil" v-model="filtertext">
  </form>
  <div class="responsive-table">
    <table dir="rtl" class="table table-bordered table-condensed">
      <thead>
        <tr>
          <th v-for="elem in headers" :key="elem[1]" scope="col">{{ elem[0] }}</th>
        </tr>
      </thead>
      <tr v-for="data in studentdata" :key="data.key" v-if="filtered(data)">
        <td v-for="elem in headers" :key="elem[1]">
           {{ data[elem[1]] }}
        </td>
      </tr>
      <tr v-if="studentdata.length === 0">
          <td :colspan="headers.length">לא נמצאו תלמידים</td>
      </tr>
    </table>
  </div>
</div>
</template>

<script>
export default {
  name: 'studentTable',
  data () {
    return {
      filtertext: '',
      filter: false
    }
  },
  computed: {
    headers () {
      return this.$store.state.s.headers
    },
    studentdata () {
      return this.$store.state.s.data
    }
  },
  methods: {
    filtered (row) {
      this.bool = (this.filtertext === '')
      for (let elem of this.headers) {
        this.bool = row[elem[1]].toLowerCase().includes(this.filtertext.toLowerCase()) | this.bool
      }

      return this.bool
    }
  },
  created () {
    Promise.all([this.$store.dispatch('fetchHeaders'), this.$store.dispatch('fetchData')])
  }
}
</script>

<style>
.container {
  margin-top: 7px;
}
.filter-btn {
  margin-bottom: 10px;
  float: right;
}
.responsive-table {
  text-align: center;
}
.table {
  border-radius: 4px;
  background: white;
}
</style>



