<template>
    <div class="container-fluid" id="login">
        <!-- <div class="row">
            <div class="col-12 filler"></div>
        </div> -->
        <div class="row justify-content-center" id="logo-row">
          <img class="logo " src="../assets/logo.png"><br>
          <a> {{ errorMessage }} </a>
      </div>
        <div class="row justify-content-center align-items-center login-row">
            <div class="d-flex col-md-4 justify-content-center">
                <div class="login-backdrop shadow">
                    <form>
                        <div class="form-group">
                            <label for="id">תעודת זהות</label>
                            <input type="text" class="form-control" id="id" v-model="id" aria-describedby="idHelp" placeholder="ת.ז."
                            oninvalid="this.setCustomValidity('הכנס תעודת זהות תקנית')" pattern="[0-9]{9}" required>
                        </div>
                        <div class="form-group">
                            <label for="password">סיסמה</label>
                            <input type="password" class="form-control" id="password" v-model="password" placeholder="סיסמה" required>
                        </div>
                        <div class="form-group form-check">
                            <input type="checkbox" class="form-check-input" id="rememberMe" v-model="rememberMe">
                            <label class="form-check-label" for="rememberMe" >זכור אותי</label>
                        </div>
                            <button type="submit" class="btn btn-primary" @click="authenticate(id, password, rememberMe)">התחבר</button>
                    </form>
                </div>
            </div>
        </div>
        <!-- <div class="row">
            <div class="col-12 filler"></div>
        </div> -->
    </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      id: '',
      password: '',
      rememberMe: false,
      errorMessage: ''
    }
  },
  methods: {
    authenticate (id, password, rememberMe) {
      this.$store.dispatch('authenticate', {id, password, rememberMe}).then(() => this.$router.push('/'))
    }
  },
  beforeCreate: function () {
    if (this.$store.getters.isAuthenticated) {
      this.$router.push('/')
    }
  }
}
</script>

<style>
#login {
  margin-top: 0.2in;
}
#logo-row {
  padding-bottom: 7vh;
}
.logo {
  height: 20vh;
  width: 20vh;
}
input.form-control:invalid {
  border-color: red;
}
.form-group{
    text-align: right;
}
.form-check-label{
    margin-right: 1.3em;
}
.login-backdrop{
    border-radius: 4px;
    background: #ffffff;
    padding: 20px 40px;
}
</style>



