<template>
<div id="NavBar" v-if="$route.name !== 'login'" dir="ltr">
<nav class="navbar navbar-dark bg-dark navbar-fixed-top navbar-expand heb">
  <div class="navbar-header">
    <b-link to="/" class="navbar-brand">Mitnadev</b-link>
  </div>
  <div class="collapse navbar-collapse login" id="content">
    <ul class="navbar-nav">
      <li class="nav-item active" v-if="!loggedIn">
        <b-link class="nav-link unselectable" unselectable="on" to="/login" > התחבר </b-link>
      </li>
      <li class="nav-item dropdown active heb" v-if="loggedIn">
        <a class="nav-link dropdown-toggle" id="userDropdown" data-toggle="dropdown">ברוכים הבאים,  {{ user.name }}</a>
        <div class="dropdown-menu" aria-labelledby="userDropdown">
          <b-link v-if="user.type === 0 || user.type === 2" class="dropdown-item" to="/student">תלמיד</b-link>
          <b-link v-if="user.type === 1 || user.type === 2" class="dropdown-item" to="/teacher">מורה</b-link>
          <div class="dropdown-divider"></div>
          <a class="dropdown-item">הגדרות</a>
          <a class="dropdown-item" style="cursor: pointer" @click="logout()">התנתקות</a>
        </div>
      </li>
    </ul>
  </div>
</nav>
</div>
</template>

<script>
export default {
  name: 'Navbar',
  data () {
    return {
      log: false
    }
  },
  computed: {
    loggedIn () {
      return this.$store.getters.isAuthenticated
    },
    user () {
      if (this.$store.state.auth.user == null) { // time for token auth
        this.$store.commit('authenticate', {token: localStorage.getItem('token')})
      }
      return this.$store.state.auth.user
    }
  },
  methods: {
    logout () {
      this.$store.dispatch('logout')
      this.$router.push('/login')
    }
  }
}
</script>

<style lang="scss">
@import "../../node_modules/bootstrap/scss/bootstrap";
#NavBar{
  height: 50px;
}
.navbar {
  width: 100%;
  height: 100%;
  padding: 0 0 0 0;
}
.heb {
  text-align: right;
}
a.logo.navbar-brand:hover {
    background-color: darkgray;
    color: black;
}
.navbar-brand{
  padding-left: 10px;
  padding-right: 10px;
  height: 100%;
  display: flex;
  align-items: center;
}
.navbar-header{
  height: 100%;
}
.navbar-text {
    padding: 0;
}
.navbar-nav{
  height: 100%;
}
 #logindropdown.pressed{
  background-color: #d6d3d3;
  color: black;
}
a#logindropdown.nav-link{
  display: flex;
  align-items: center;
  height: 100%;
}
.login{
  display: flex;
  justify-content: flex-end;
  height: 100%;
}
a.nav-link{
  height: 100%;
  margin: auto;
  cursor: pointer;
}
a.nav-link:hover {
    background-color: darkgray;
    color: black;
}
.dropdown-item {
  text-align: right;
}
*.unselectable {
   -moz-user-select: -moz-none;
   -khtml-user-select: none;
   -webkit-user-select: none;
   /*
     Introduced in IE 10.
     See http://ie.microsoft.com/testdrive/HTML5/msUserSelect/
   */
   -ms-user-select: none;
   user-select: none;
}
</style>



