<template>
  <div id="student" class="container-fluid">
    <div class="modal" tabindex="-1" id="first" role="dialog" data-backdrop="static">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">נראה כאילו התחברת בפעם הראשונה, האם תרצה להגדיר את המשתמש?</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>(ניתן לעשות זאת מאוחר יותר ע"י לחיצה על משתמש > הגדרות)</p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" @click="notFirstAnymore()">מאוחר יותר</button>
            <button type="button" class="btn btn-primary" @click="closeModal();$router.push('/settings')">כן</button>
          </div>
        </div>
      </div>
    </div>
    <a>student stuff goes here</a>
  </div>
</template>

<script>
export default {
  name: 'student',
  computed: {
    user () {
      return this.$store.state.auth.user
    }
  },
  methods: {
    async notFirstAnymore () {
      await this.$store.dispatch('removeFirst')
      // eslint-disable-next-line
      $('#first').hide()
    },
    closeModal () {
      $('#first').hide()
    }
  },
  created () {
    if (!!this.$store.state.auth.user && this.$store.state.auth.user.firstLogin) {
      // eslint-disable-next-line
      $('#first').modal()
    }
  }
}
</script>

<style scoped>
.modal {
  display: block;
  text-align: right;
}
</style>



