import axios from 'axios'

const API = 'http://localhost:3000/api'

const state = {
  headers: [],
  data: []
}

const getters = {
  // will make an api call, this is just for testing
  data: (state, getters, rootState) => {
    if (rootState.auth.user != null && (rootState.auth.user.type === 1 || rootState.auth.user.type === 2)) {
      return [
        { key: 12, first: 'Oded', last: 'Shapira', hand: 'I am not sure' },
        { key: 15, first: 'Ido', last: 'Shavit', hand: 'Please' },
        { key: 17, first: 'Nir', last: 'Shahar', hand: 'ree' },
        { key: 18, first: 'Noa', last: 'Shapira', hand: 'pool' }
      ]
    }
    return []
  }
}

const mutations = {
  fetchHeaders (state, data) {
    state.headers = data.headers
  },
  fetchData (state, data) {
    state.data = data.data
  }
}

const actions = {
  async fetchHeaders ({ commit, rootState }) {
    let config = {}
    // eslint-disable-next-line no-extra-boolean-cast
    if (!!rootState.auth.user) {
      config = {headers: {'Authorization': rootState.auth.user.secret}}
    }
    const response = await axios.get(API + '/headers', config)
    commit('fetchHeaders', response.data)
  },
  async fetchData ({ commit, rootState }) {
    let config = {}
    // eslint-disable-next-line no-extra-boolean-cast
    if (!!rootState.auth.user) {
      config = {headers: {'Authorization': rootState.auth.user.secret}}
    }
    const response = await axios.get(API + '/studentdata', config)
    commit('fetchData', response.data)
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



