const state = {
  user: null
}

const getters = {
  isAuthenticated: state => {
    return localStorage.getItem('token') != null || state.user !== null
  },
  isTeacher: state => {
    if (state.user == null) {
      return false
    }
    return state.user.type === 1 || state.user.type === 2
  },
  isStudent: state => {
    if (state.user == null) {
      return false
    }
    return state.user.type === 2 || state.user.type === 0
  }
}

const mutations = {
  authenticate (state, data) {
    state.user = data
    if (data.rememberMe) {
      localStorage.setItem('token', state.user.secret)
    }
  },
  logout (state) {
    state.user = null
    localStorage.removeItem('token')
  },
  removeFirst () {
    if (!state.user) {
      return
    }
    state.user.firstLogin = false
  }
}

const actions = {
  async authenticate ({ commit }, data) {
    // API CALL HERE TO RECEIVE AUTH DETAILS SHOULD BE CLIENT SECRET + ID + EMAIL AND STUFF LIKE THAT
    /*
    types:
    0 - student
    1 - teacher
    2 - administrator
    */
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        commit('authenticate', { secret: 'secret', id: data.id, name: 'עודד', type: 2, firstLogin: true })
        resolve()
      }, 1000)
    })
  },
  logout ({ commit }) {
    commit('logout')
  },
  async removeFirst ({ commit }) {
    setTimeout(() => {
      commit('removeFirst')
    }, 1000)
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



